@tailwind base;
@tailwind components;
@tailwind utilities;
/*main-color*/
/*gray-color*/
/*backgrounds-color*/
/*to hide arrows from input => number */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0 !important;
}

input[type=number] {
  -moz-appearance: textfield !important;
}

/*to hide arrows from input => number */
/*fonts */
.font-cairo {
  font-family: "Cairo" !important;
}

/*background of page */
.App {
  background-image: url(../images/backgroundImage.PNG);
  font-family: "Cairo";
  background-size: cover;
}

.gray-color {
  color: #BBBBBB !important;
  font-weight: 600;
}

.main-color {
  color: #FE8E00 !important;
  font-weight: 700;
}

.main-color-hover {
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.main-color-hover:hover {
  color: #FE8E00 !important;
}

.gray-2-color {
  color: #DFDFDF;
}

.gray-3-color {
  color: #FAFBFE !important;
}

.text-main {
  color: #FE8E00;
  font-weight: 500;
}

.border-gray, .input-code:focus {
  border-color: #BBBBBB !important;
}

.border-main {
  border-color: #FE8E00 !important;
}

.border-2-gray {
  border-color: #DFDFDF !important;
}

.background-gray {
  background-color: #BBBBBB;
}

.background-gray-3 {
  background-color: #FAFBFE;
}

.background-main-color {
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
}
.background-main-color:hover {
  color: #FAFBFE !important;
  background-color: #FE8E00;
