@tailwind base;
@tailwind components;
@tailwind utilities;

/*main-color*/
$main-color: #FE8E00;

/*gray-color*/
$gray-color: #BBBBBB;
$gray-2-color: #DFDFDF;
$gray-3-color: #FAFBFE;

/*backgrounds-color*/
$backgrounds-color: #ffffff;

/*to hide arrows from input => number */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0 !important;
}

input[type=number] {
  -moz-appearance: textfield !important;
}
/*to hide arrows from input => number */

/*fonts */
.font-cairo{
    font-family: 'Cairo' !important;
}

/*background of page */
.App {
    // background-color: $backgrounds-color;
    background-image: url(../images/backgroundImage.PNG);
    font-family: 'Cairo';
    background-size: cover;
}

.gray-color {
    color: $gray-color !important;
    font-weight: 600;
}

.main-color {
    color: $main-color !important;
    font-weight: 700;
}

.main-color-hover {
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;

    &:hover {
        color: $main-color !important;
    }
}

.gray-2-color {
    color: $gray-2-color;
}

.gray-3-color {
    color: $gray-3-color !important;
}

.text-main {
    color: $main-color;
    font-weight: 500;
}

.border-gray {
    border-color: $gray-color !important;
}

.border-main {
    border-color: $main-color !important;
}

.border-2-gray {
    border-color: $gray-2-color !important;
}

.background-gray {
    background-color: $gray-color;
}

.background-gray-3 {
    background-color: $gray-3-color;
}

.background-main-color {
    transition: 0.2s;
    -webkit-transition: 0.2s;
    -moz-transition: 0.2s;
    -ms-transition: 0.2s;
    -o-transition: 0.2s;

    &:hover {
        color: $gray-3-color !important;
        background-color: $main-color;
    }
}

.input-code {
    &:focus{
       @extend .border-gray
    }
}
